<template>
  <div>
    <p>
      <span class="is-size-5">
        You're almost there!
      </span>
    </p>

    <p>
      You have
      <strong class="has-text-primary">
        {{ allocationLeft }}
      </strong>
      {{ plural ? "opportunities" : "opportunity" }}
      left to purchase tickets for others, or invite them to purchase their own.
      There
      {{ pluralTickets ? "are" : "is" }} currently
      <strong class="has-text-primary">{{ ticketsLeft }}</strong>
      {{ pluralTickets ? "tickets" : "ticket" }} left. Don't miss out!
    </p>
  </div>
</template>

<script>
export default {
  name: "CopyInviteAndBuy",
  props: {
    allocationLeft: {
      type: Number,
      required: true
    },
    event: {
      type: Object,
      required: true
    }
  },
  computed: {
    plural() {
      return this.allocationLeft !== 1;
    },
    pluralTickets() {
      return this.ticketsLeft > 1;
    },
    ticketsLeft() {
      if (this.event.standardTicketsLeft > 3) {
        return 3;
      } else {
        return this.event.standardTicketsLeft;
      }
    }
  }
};
</script>
