<template>
  <div>
    <template v-if="invitedBy && paidFor">
      <p v-if="invitedBy && paidFor">
        <strong class="has-text-primary">
          {{ invitedByName }}
        </strong>
        has bought you a ticket.
      </p>
      <p>
        Update your details below to claim it.
      </p>
    </template>
    <p v-else-if="invitedBy">
      You have been invited by
      <strong class="has-text-primary"> {{ invitedByName }} </strong>. This
      means you got to skip the application process.
    </p>
    <p v-if="!paidFor">
      You have the opportunity to purchase
      <strong class="has-text-primary">
        {{ ticketOffer.ticketCount }}
      </strong>
      {{ plural ? "tickets" : "ticket" }} for
      <strong class="has-text-primary">{{ formattedTicketPrice }}</strong>
      {{ plural ? "each" : null }}. There {{ pluralTickets ? "are" : "is" }}
      <strong class="has-text-primary">{{ ticketsLeft }}</strong>
      {{ pluralTickets ? "tickets" : "ticket" }} left, please complete the form
      below and buy your {{ plural ? "tickets" : "ticket" }} now.
    </p>
  </div>
</template>

<script>
export default {
  name: "CopyTicketOffer",
  props: {
    ticketOffer: {
      type: Object,
      required: true
    },
    event: {
      type: Object,
      required: true
    }
  },
  computed: {
    plural() {
      return this.ticketOffer.ticketCount > 1;
    },
    formattedTicketPrice() {
      return `$${this.ticketOffer.costAfterDiscountCents / 100}`;
    },
    paidFor() {
      return this.ticketOffer.invitePaidFor;
    },
    invitedBy() {
      return this.ticketOffer.invitedBy;
    },
    invitedByName() {
      return `${this.invitedBy.firstName} ${this.invitedBy.lastName}`;
    },
    pluralTickets() {
      return this.ticketsLeft > 1;
    },
    ticketsLeft() {
      if (this.event.standardTicketsLeft > 3) {
        return 3;
      } else {
        return this.event.standardTicketsLeft;
      }
    }
  }
};
</script>
