<template>
  <ticket-offer-spaces-full
    v-if="event.soldOut && ticketOffer.status !== 'purchased'"
  />
  <vip-invite-pending v-else-if="ticketOffer.status === 'pending'" />
  <ticket-offer-purchased v-else-if="ticketOffer.status === 'purchased'" />
</template>

<script>
import TicketOfferSpacesFull from "@/ticketing/views/TicketOfferSpacesFull";
import VipInvitePending from "@/ticketing/views/VipInvitePending";
import TicketOfferPurchased from "@/ticketing/views/TicketOfferPurchased";
import { mapGetters } from "vuex";

export default {
  name: "VipInvite",
  components: {
    TicketOfferSpacesFull,
    VipInvitePending,
    TicketOfferPurchased
  },
  data() {
    return {
      status: {
        pending: "partner-registration"
      }
    };
  },
  computed: {
    ...mapGetters({
      event: "events/event",
      person: "people/person",
      ticketOffer: "ticketOffers/ticketOffer"
    })
  }
};
</script>
