<template>
  <aside>
    <h3 class="title is-4">Order Tally</h3>

    <h4 class="title is-5">Tickets</h4>
    <OrderTallyRow
      :invite="person"
      :price="formattedTicketPrice"
      forPurchaser
    />

    <div v-for="(invite, index) in invites">
      <OrderTallyRow
        v-if="invite.paidFor"
        :key="invite.email"
        :invite="invite"
        :price="formattedTicketPrice"
        position="paid"
        @remove-tally-row="$emit('remove-invite', invite.id)"
        @toggle-invite-paid="$emit('toggle-invite-paid', invite.id)"
      />
    </div>

    <h4 class="title is-5">Invites</h4>
    <div v-for="(invite, index) in invites">
      <OrderTallyRow
        v-if="!invite.paidFor"
        :key="invite.email"
        :invite="invite"
        :price="formattedTicketPrice"
        position="unpaid"
        @remove-tally-row="$emit('remove-invite', invite.id)"
        @toggle-invite-paid="$emit('toggle-invite-paid', invite.id)"
      />
    </div>

    <div class="tally-row">
      <div class="tally-row__name">
        total:
      </div>
      <div class="tally-row__price">
        {{ formattedOrderTotal }}
      </div>
    </div>

    <button
      class="button is-primary is-medium is-fullwidth"
      type="button"
      role="link"
      @click.prevent="$emit('checkout')"
    >
      Checkout
    </button>
  </aside>
</template>

<script>
import OrderTallyRow from "@/ticketing/components/OrderTallyRow";

export default {
  name: "OrderTally",
  components: {
    OrderTallyRow
  },
  props: {
    person: {
      type: Object,
      required: true
    },
    paidInviteCount: {
      type: Number,
      required: true
    },
    invites: {
      type: Array,
      required: true
    },
    ticketPrice: {
      type: Number,
      required: true
    }
  },
  computed: {
    formattedTicketPrice() {
      return `$${this.ticketPrice / 100}`;
    },
    orderTotal() {
      return (1 + this.paidInviteCount) * this.ticketPrice;
    },
    formattedOrderTotal() {
      return `$${this.orderTotal / 100}`;
    },
    plural() {
      return this.ticketOffer.ticketCount > 1;
    }
  }
};
</script>

