<template>
  <div class="section">
    <page-content :contentId="contentId" />

    <section class="container" v-if="$v.person">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="register">
            <h2 class="title has-text-poppins is-5 has-red-stripe">
              let's first confirm a few details about you
            </h2>

            <b-field
              label="First Name"
              :type="{ 'is-danger': $v.person.firstName.$error }"
              :message="
                $v.person.firstName.$error ? 'Please enter your first name' : ''
              "
            >
              <b-input
                v-model="person.firstName"
                placeholder="Leslie"
                @blur="$v.person.firstName.$touch()"
                type="text"
                icon="account"
              >
              </b-input>
            </b-field>

            <b-field
              label="Last Name"
              :type="{ 'is-danger': $v.person.lastName.$error }"
              :message="
                $v.person.lastName.$error ? 'Please enter your last name' : ''
              "
            >
              <b-input
                v-model="person.lastName"
                placeholder="Miles"
                @blur="$v.person.lastName.$touch()"
                type="text"
                icon="account"
              >
              </b-input>
            </b-field>

            <b-field
              label="Email"
              :type="{
                'is-danger': $v.person.email.$error || errors.email
              }"
              :message="errors.email ? errors.email : ''"
            >
              <b-input
                v-model="person.email"
                placeholder="francis@example.com"
                @blur="$v.person.email.$touch()"
                type="email"
                icon="email"
              >
              </b-input>
            </b-field>

            <fieldset>
              <legend class="label">
                Do you have any Dietary Requirements?
              </legend>
              <div class="checkbox-row">
                <b-checkbox
                  v-for="requirement in dietaryRequirements"
                  :key="requirement"
                  class="checkbox-column"
                  v-model="person.data.dietaryRequirements"
                  :native-value="requirement"
                >
                  {{ requirement }}
                </b-checkbox>
              </div>
            </fieldset>

            <fieldset>
              <legend class="label">
                Do you have any Accessibility Requirements?
              </legend>
              <div class="checkbox-row">
                <b-checkbox
                  v-for="requirement in accessabilityRequirements"
                  :key="requirement"
                  class="checkbox-column"
                  v-model="person.data.accessabilityRequirements"
                  :native-value="requirement"
                >
                  {{ requirement }}
                </b-checkbox>
              </div>
            </fieldset>

            <div class="actions-row buttons is-pulled-right">
              <input
                class="button is-medium is-primary"
                type="submit"
                value="Proceed to Invites and Purchase"
              />
            </div>
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import PageContent from "@/ticketing/components/PageContent";
import ProfileService from "@/services/ProfileService";
import NProgress from "nprogress";
import { required, email } from "vuelidate/lib/validators";
import { markdownSubstitutionsMixin } from "@/mixins/markdownSubstitutionsMixin";
import { mapGetters } from "vuex";

export default {
  name: "VipInvitePending",
  components: {
    PageContent
  },
  validations: {
    person: {
      firstName: { required },
      lastName: { required },
      email: { required, email }
    }
  },
  mixins: [markdownSubstitutionsMixin],
  data() {
    return {
      contentName: "vip_ticket_offer_page_content",
      errors: {}
    };
  },
  methods: {
    register() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();
        ProfileService.updateProfile(this.person.id, {
          person: this.person,
          profile: this.person.data
        })
          .then(() => {
            NProgress.done();
            this.$router.push({
              name: "ticket-offer-purchase",
              params: {
                eventSlug: this.event.slug,
                id: this.ticketOffer.id
              }
            });
          })
          .catch(error => {
            console.log(error);
          });
      }
    }
  },
  computed: {
    ...mapGetters(["dietaryRequirements", "accessabilityRequirements"]),
    ...mapGetters({
      event: "events/event",
      person: "people/person",
      ticketOffer: "ticketOffers/ticketOffer"
    }),
    contentId() {
      return this.event.pageContent.find(c => c[0] === this.contentName)[1];
    }
  }
};
</script>

<style scoped>
.actions-row {
  margin-top: 1rem;
}
</style>
