<template>
  <div>

    <ticket :person="person" :event="event" :ticketOffer="ticketOffer" />

    <section class="section">
      <page-content class="hide-print" :contentId="contentId" />

      <section class="container hide-print">
        <div class="columns is-centered">
          <div class="column is-6">
            <form @submit.prevent="updateInfo">
              <fieldset>
                <legend class="label">
                  Dietary Requirements
                </legend>
                <div class="checkbox-row">
                  <b-checkbox
                    v-for="requirement in dietaryRequirements"
                    :key="requirement"
                    class="checkbox-column"
                    v-model="person.data.dietaryRequirements"
                    :native-value="requirement"
                  >
                    {{ requirement }}
                  </b-checkbox>
                </div>
              </fieldset>

              <fieldset>
                <legend class="label">
                  Accessibility Requirements
                </legend>
                <div class="checkbox-row">
                  <b-checkbox
                    v-for="requirement in accessabilityRequirements"
                    :key="requirement"
                    class="checkbox-column"
                    v-model="person.data.accessabilityRequirements"
                    :native-value="requirement"
                  >
                    {{ requirement }}
                  </b-checkbox>
                </div>
              </fieldset>

              <div class="mt-5 buttons is-medium is-pulled-right">
                <input
                  class="button"
                  type="submit"
                  value="Update info"
                />
                <a :href="receiptUrl"
                  v-if="!ticketOffer.invitePaidFor"
                  target="_blank"
                  class="button"
                  rel="noopener noreferrer">
                  GST Receipt
                </a>
              </div>
            </form>
          </div>
        </div>
      </section>
    </section>
  </div>
</template>

<script>
import Ticket from "@/components/Ticket";
import PageContent from "@/ticketing/components/PageContent";
import ProfileService from "@/services/ProfileService";
import NProgress from "nprogress";
import { personNameMixin } from "@/mixins/personNameMixin";
import { required, email } from "vuelidate/lib/validators";
import { mapGetters } from "vuex";

export default {
  name: "TicketOfferPurchased",
  components: { PageContent, Ticket },
  mixins: [personNameMixin],
  validations: {
    person: {
      firstName: { required },
      lastName: { required },
      email: { required, email }
    }
  },
  beforeMount() {
    this.$store.dispatch("layouts/setBannerVisibility", false);
  },
  data() {
    return {
      contentName: "purchased_page_content"
    };
  },
  computed: {
    ...mapGetters(["dietaryRequirements", "accessabilityRequirements"]),
    ...mapGetters({
      event: "events/event",
      person: "people/person",
      ticketOffer: "ticketOffers/ticketOffer"
    }),
    contentId() {
      return this.event.pageContent.find(c => c[0] === this.contentName)[1];
    },
    paidFor() {
      return this.ticketOffer.invitePaidFor;
    },
    invitedBy() {
      return this.ticketOffer.invitedBy;
    },
    invitedByName() {
      return `${this.invitedBy.firstName} ${this.invitedBy.lastName}`;
    },
    receiptUrl() {
      const baseURL = process.env.VUE_APP_PDF_BASE_URL
      return `${baseURL}/receipts/${this.ticketOffer.id}`
    }
  },
  methods: {
    updateInfo() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();

        ProfileService.updateProfile(this.person.id, {
          profile: this.person.data
        })
          .then(() => {
            NProgress.done();
            this.$snackbar.open(`Successfully updated special requirements`);
          })
          .catch(e => {
            NProgress.done();
            console.log("ERROR", e);
          });
      }
    }
  }
};
</script>
