<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">Update Ticket Details</h1>
          <h1 class="subtitle is-2">{{ event.title }}</h1>
        </div>
      </div>
    </section>

    <section class="container section-spacing content">
      <div class="columns is-centered">
        <div class="column is-7">
          <p class="is-size-4">
            Hi
            <span class="has-text-primary">{{ personName }}</span>
          </p>

          <p>
            Please update your Ticket information.
          </p>

          <p>
            <strong class="has-text-primary">
              The TEDxAuckland team
            </strong>
          </p>
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="updateInfo">
            <h2 class="title has-text-poppins is-5 has-red-stripe">
              Your Contact Info
            </h2>

            <b-field
              label="First Name"
              :type="{ 'is-danger': $v.personAnswers.firstName.$error }"
              :message="
                $v.personAnswers.firstName.$error
                  ? 'Please enter your first name'
                  : ''
              "
            >
              <b-input
                v-model="personAnswers.firstName"
                placeholder="Leslie"
                @blur="$v.personAnswers.firstName.$touch()"
                type="text"
                icon="account"
              >
              </b-input>
            </b-field>

            <b-field
              label="Last Name"
              :type="{ 'is-danger': $v.personAnswers.lastName.$error }"
              :message="
                $v.personAnswers.lastName.$error
                  ? 'Please enter your last name'
                  : ''
              "
            >
              <b-input
                v-model="personAnswers.lastName"
                placeholder="Miles"
                @blur="$v.personAnswers.lastName.$touch()"
                type="text"
                icon="account"
              >
              </b-input>
            </b-field>

            <b-field
              label="Email"
              :type="{
                'is-danger': $v.personAnswers.email.$error || errors.email
              }"
              :message="errors.email ? errors.email : ''"
            >
              <b-input
                v-model="personAnswers.email"
                placeholder="francis@example.com"
                @blur="$v.personAnswers.email.$touch()"
                type="email"
                icon="email"
              >
              </b-input>
            </b-field>

            <fieldset>
              <legend class="label">
                Do you have any Dietary Requirements?
              </legend>
              <div class="checkbox-row">
                <b-checkbox
                  v-for="requirement in dietaryRequirements"
                  :key="requirement"
                  class="checkbox-column"
                  v-model="personAnswers.data.dietaryRequirements"
                  :native-value="requirement"
                >
                  {{ requirement }}
                </b-checkbox>
              </div>
            </fieldset>

            <fieldset>
              <legend class="label">
                Do you have any Accessibility Requirements?
              </legend>
              <div class="checkbox-row">
                <b-checkbox
                  v-for="requirement in accessabilityRequirements"
                  :key="requirement"
                  class="checkbox-column"
                  v-model="personAnswers.data.accessabilityRequirements"
                  :native-value="requirement"
                >
                  {{ requirement }}
                </b-checkbox>
              </div>
            </fieldset>

            <input
              class="button is-medium is-pulled-right is-primary"
              type="submit"
              value="Update info"
            />
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import ProfileService from "@/services/ProfileService";
import NProgress from "nprogress";
import { required, email } from "vuelidate/lib/validators";

import { personNameMixin } from "@/mixins/personNameMixin";

export default {
  name: "TicketDetails",
  props: {
    person: {
      type: Object,
      required: true
    },
    ticketOffer: {
      type: Object,
      required: true
    },
    event: {
      type: Object,
      required: true
    }
  },
  mixins: [personNameMixin],
  validations: {
    personAnswers: {
      firstName: { required },
      lastName: { required },
      email: { required, email }
    }
  },
  data() {
    return {
      personAnswers: this.person,
      errors: {}
    };
  },
  methods: {
    snackbar() {
      this.$snackbar.open(
        `Default, positioned bottom-right with a green 'OK' button`
      );
    },
    updateInfo() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();

        ProfileService.updateProfile(this.person.id, {
          person: {
            firstName: this.personAnswers.firstName,
            lastName: this.personAnswers.lastName,
            email: this.personAnswers.email
          },
          profile: this.personAnswers.data
        })
          .then(() => {
            NProgress.done();
            this.$snackbar.open(`Successfully updated Ticket Details`);
          })
          .catch(e => {
            NProgress.done();
            console.log("ERROR", e);
          });
      }
    }
  },
  computed: {
    dietaryRequirements() {
      return this.$store.state.dietaryRequirements;
    },
    accessabilityRequirements() {
      return this.$store.state.accessabilityRequirements;
    }
  }
};
</script>

<style>
.link {
  text-decoration: underline;
}
</style>
