<template>
  <ticket-offer-spaces-full
    v-if="event.soldOut && ticketOffer.status !== 'purchased'"
  />
  <ticket-sales-closed
    v-else-if="!event.ticketSalesActive && ticketOffer.status !== 'purchased'"
  />
  <div v-else>
    <page-content :contentId="contentId" :titleColumns="9" :bodyColumns="8">
      <CopyInviteAndBuy
        slot="afterContent"
        v-if="event.has_applications"
        :allocationLeft="allocationLeft"
        :event="event"
      />
    </page-content>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-9">
          <div class="columns">
            <div class="column is-8">
              <div class="ticket-container">
                <h2 class="title is-3">Your Ticket Details</h2>

                <TicketPreview
                  :person="person"
                  :ticket-offer="ticketOffer"
                  :event="event"
                />
              </div>

              <div v-if="!allocationReached" class="ticket-container columns">
                <div class="column">
                  <h2 class="title is-3">Invite Your Friends and Family</h2>

                  <RecipientDetailsForm
                    :newInvite="newInvite"
                    :allocationReached="allocationReached"
                    @add-invite="addInvite($event)"
                  />
                </div>
              </div>
            </div>
            <div class="column is-4">
              <OrderTally
                :person="person"
                :invites="invites"
                :paidInviteCount="paidInviteCount"
                :ticketPrice="ticketOffer.costAfterDiscountCents * 1"
                @remove-invite="removeInvite($event)"
                @toggle-invite-paid="toggleInvitePaid($event)"
                @checkout="checkout"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import TicketOfferSpacesFull from "@/ticketing/views/TicketOfferSpacesFull";
import TicketSalesClosed from "@/ticketing/views/TicketSalesClosed";
import { required, email } from "vuelidate/lib/validators";
import PageContent from "@/ticketing/components/PageContent";
import CopyInviteAndBuy from "@/ticketing/components/CopyInviteAndBuy";
import TicketPreview from "@/ticketing/components/TicketPreview";
import RecipientDetailsForm from "@/ticketing/components/RecipientDetailsForm";
import OrderTally from "@/ticketing/components/OrderTally";
import TicketService from "@/services/TicketService";
import NProgress from "nprogress";
import { personNameMixin } from "@/mixins/personNameMixin";

import { mapGetters } from "vuex";

export default {
  name: "TicketOfferPurchase",
  components: {
    TicketOfferSpacesFull,
    TicketSalesClosed,
    PageContent,
    CopyInviteAndBuy,
    TicketPreview,
    RecipientDetailsForm,
    OrderTally
  },
  props: {
    existingInvites: {
      type: Array,
      required: true
    },
    newInvite: {
      type: Object,
      required: true
    }
  },
  mixins: [personNameMixin],
  data() {
    return {
      isRejectModalActive: false,
      invites: this.existingInvites,
      invitesOld: [],
      contentName: "invite_page_content"
    };
  },
  computed: {
    ...mapGetters({
      event: "events/event",
      person: "people/person",
      ticketOffer: "ticketOffers/ticketOffer",
      content: "content/contentByField"
    }),
    contentId() {
      return this.event.pageContent.find(c => c[0] === this.contentName)[1];
    },
    paidInvites() {
      return this.invites.filter(invite => invite.paidFor);
    },
    paidInviteCount() {
      return this.paidInvites.length;
    },
    allocationLeft() {
      return this.ticketOffer.ticketCount - 1 - this.invites.length;
    },
    allocationReached() {
      return this.allocationLeft <= 0;
    }
  },
  validations: {
    invites: {
      required,
      $each: {
        firstName: { required },
        lastName: { required },
        email: { required, email },
        paidFor: {}
      }
    }
  },
  methods: {
    addInvite(invite) {
      if (this.allocationReached) {
        return null;
      } else {
        invite.ticketOfferId = this.ticketOffer.id;
        TicketService.createInvite(this.event.slug, this.ticketOffer.id, invite)
          .then(res => {
            this.invites = res.data.invites;
            if (res.data.error) {
              this.$snackbar.open({
                message: res.data.error,
                type: "is-danger",
                position: "is-bottom-left"
              });
            }
          })
          .catch(e => {
            console.log("create invite error", e);
          });
      }
    },
    removeInvite(id) {
      TicketService.deleteInvite(id)
        .then(res => {
          this.invites = res.data.invites;
        })
        .catch(e => {
          console.log("delete invite error", e);
        });
    },
    toggleInvitePaid(id) {
      let invite = this.invites.find(invite => invite.id === id);
      TicketService.updateInvite(id, {
        ...invite,
        paidFor: !invite.paidFor
      })
        .then(res => {
          this.invites = res.data.invites;
        })
        .catch(e => {
          console.log("update invite error", e);
        });
    },
    checkout() {
      NProgress.start();

      TicketService.createStripeSession({
        ticketOfferId: this.ticketOffer.id,
        eventId: this.event.id
      }).then(response => {
        const publishableKey = process.env.VUE_APP_STRIPE_KEY;

        const stripe = Stripe(publishableKey);
        stripe
          .redirectToCheckout({
            sessionId: response.data.stripeSessionId
          })
          .then(result => {
            // If `redirectToCheckout` fails due to a browser or network
            // error, display the localized error message to your customer
            // using `result.error.message`.
          });
      });
    }
  }
};
</script>

<style scoped>
.add-button {
  margin-right: 1rem;
}
</style>
