<template>
  <div class="tally-row">
    <div class="tally-row__name">
      <b-tooltip
        v-if="!forPurchaser"
        :label="`Remove ${invite.firstName}`"
        class="tally-row__button"
      >
        <b-icon icon="close" @click.native="$emit('remove-tally-row')" />
      </b-tooltip>

      <b-tooltip
        v-if="!forPurchaser"
        :label="`Move ${invite.firstName} to ${moveText}`"
        class="tally-row__button"
      >
        <b-icon :icon="moveIcon" @click.native="$emit('toggle-invite-paid')" />
      </b-tooltip>

      {{ invite.firstName }}
    </div>
    <div class="tally-row__price">
      <span v-if="invite.paidFor || forPurchaser">
        {{ price }}
      </span>
      <span v-else>
        free
      </span>
    </div>
  </div>
</template>

<script>
export default {
  name: "OrderTallyRow",
  props: {
    invite: {
      type: Object,
      required: true
    },
    price: {
      type: String,
      required: true
    },
    position: {
      type: String,
      default: "paid"
    },
    forPurchaser: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    moveText() {
      return {
        paid: "unpaid",
        unpaid: "paid"
      }[this.position];
    },
    moveIcon() {
      return {
        paid: "arrow-down-bold-outline",
        unpaid: "arrow-up-bold-outline"
      }[this.position];
    }
  }
};
</script>

<style lang="scss">
.tally-row {
  display: flex;
  justify-content: space-between;
  margin: 1rem 0;

  &__name {
    font-weight: 500;
  }

  &__price {
    font-size: 1.1em;
  }
  &__button {
    cursor: pointer;
    transform: translateY(0.2rem);
  }
}
</style>
