<template>
  <ticket-offer-spaces-full
    v-if="event.soldOut && ticketOffer.status !== 'purchased'"
  />
  <ticket-sales-closed
    v-else-if="!event.ticketSalesActive && ticketOffer.status !== 'purchased'"
  />
  <ticket-offer-purchased-update-details
    v-else-if="ticketOffer.status === 'pending' && ticketOffer.invitedBy && !ticketOffer.invitePaidFor && needsDetails"
    @recalcNeeded="recalcNeedsDetails()"
  />
  <ticket-offer-pending v-else-if="ticketOffer.status === 'pending'" />
  <ticket-offer-purchased-update-details
    v-else-if="ticketOffer.status === 'purchased' && needsDetails"
    @recalcNeeded="recalcNeedsDetails()"
  />
  <ticket-offer-purchased v-else-if="ticketOffer.status === 'purchased'" />
</template>

<script>
import TicketOfferSpacesFull from "@/ticketing/views/TicketOfferSpacesFull";
import TicketSalesClosed from "@/ticketing/views/TicketSalesClosed";
import TicketOfferPending from "@/ticketing/views/TicketOfferPending";
import TicketOfferPurchasedUpdateDetails from "@/ticketing/views/TicketOfferPurchasedUpdateDetails";
import TicketOfferPurchased from "@/ticketing/views/TicketOfferPurchased";

import ProfileService from "@/services/ProfileService"

import { mapGetters } from "vuex";

export default {
  name: "TicketOffer",
  components: {
    TicketOfferSpacesFull,
    TicketSalesClosed,
    TicketOfferPending,
    TicketOfferPurchasedUpdateDetails,
    TicketOfferPurchased
  },
  beforeMount() {
    this.needsDetails = ProfileService.calcNeedsDetails(this.person);
  },
  data() {
    return {
      needsDetails: null
    };
  },
  computed: {
    ...mapGetters({
      event: "events/event",
      person: "people/person",
      ticketOffer: "ticketOffers/ticketOffer"
    })
  },
  methods: {
    recalcNeedsDetails() {
      this.$store.dispatch("people/getPerson", this.person.id).then(() => {
        ProfileService.calcNeedsDetails(this.person);
      });
    },
  }
};
</script>
